<template>
  <main v-if="page.id">
    <!-- <Breadcrumbs v-if="isBreadcrumbs" /> -->

    <section class="seo-page" v-if="page.content">
      <div class="container" v-html="page.content"></div>
    </section>

    <template v-if="page.blocks">
      <template v-for="(block, b) in page.blocks">
        <Contacts :key="b" v-if="block.type === 'contacts'" :data="block" />
        <Messengers :key="b" v-if="block.type === 'messengers'" :data="block" />
        <CallOrder :key="b" v-if="block.type === 'call'" :data="block" />
        <Requisites :key="b" v-if="block.type === 'requisites'" :data="block" />
        <Join :key="b" v-if="block.type === 'join'" :data="block" />
      </template>
    </template>
  </main>
</template>

<script>
import Breadcrumbs from "../components/Breadcrumbs.vue";
import Messengers from "../components/Messengers.vue";
import Contacts from "../components/Contacts.vue";
import CallOrder from "../components/CallOrder.vue";
import Requisites from "../components/Requisites.vue";
import Join from "../components/Join.vue";

export default {
  components: {
    Breadcrumbs,
    Contacts,
    Messengers,
    CallOrder,
    Requisites,
    Join
  },
  computed: {
    page() {
      return this.$store.state.page;
    },
    isBreadcrumbs() {
      if (!this.page.blocks) {
        return true;
      }
      return !["banner", "contacts"].includes(this.page.blocks[0].type);
    }
  }
};
</script>
