<template>
  <div
    class="search search--header"
    @click="SearchInit()"
    :class="{ active: active }"
  >
    <input
      type="search"
      name="search"
      id="search"
      class="input search__input search__input--header"
      :class="{ active: active }"
      :placeholder="placeholder"
    />
    <button class="button button--search">
      <svg width="20" height="19" aria-hidden="true" class="icon">
        <use xlink:href="#search"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      active: false,
      placeholder: "Я ищу..."
    };
  },
  computed: {},
  methods: {
    SearchInit(e) {
      if (this.$root.width > 767) {
        this.active = true;
      }
    }
  },
  created() {
    document.addEventListener("click", e => {
      if (!e.target.classList.value.includes("header")) {
        this.active = false;
      }
    });
  },
  mounted() {},
  beforeDestroy() {},
  destroyed() {},
  components: {}
};
</script>
