<template>
  <section
    id="reviews"
    :class="'reviews ' + data.class"
    v-if="data && data.enable"
  >
    <div class="container" v-if="!isMobile">
      <div class="reviews__titles">
        <h2 class="title" v-html="data.title"></h2>
        <div class="reviews__more">
          <a href="#" v-html="data.more"></a>
        </div>
      </div>

      <swiper
        class="swiper reviews__swiper"
        ref="swiperBig"
        :options="swiperOptions"
      >
        <swiper-slide
          class="reviews__slide"
          v-for="(review, k) in data.reviews"
          :key="k"
        >
          <p class="reviews__content" v-html="review.content"></p>
          <div class="reviews__info">
            <div class="reviews__details">
              <div class="reviews__author-info">
                <div class="reviews__author-pic">
                  <img :src="review.avatar" :alt="review.author" />
                </div>
                <div class="reviews__author-details">
                  <p class="reviews__author" v-html="review.author"></p>
                  <span
                    class="reviews__date"
                    v-html="
                      $dayjs(review.date)
                        .format(`D MMM YYYYY, HH:mm`)
                        .replace('+0300', '')
                    "
                  ></span>
                </div>
              </div>
              <a
                class="reviews__link"
                :href="review.link"
                target="_blank"
                v-html="review.linkText"
              ></a>
            </div>
          </div>
        </swiper-slide>
      </swiper>
      <button
        class="button swiper-reviews-prev swiper-button-prev"
        slot="button-prev"
      >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#arrow-white"></use>
        </svg>
      </button>
      <button
        class="button swiper-reviews-next swiper-button-next"
        slot="button-next"
      >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#arrow-white"></use>
        </svg>
      </button>
    </div>
    <div v-else>
      <div class="container">
        <div class="reviews__titles">
          <h2 class="title" v-html="data.title"></h2>
          <div class="reviews__more">
            <a href="#" v-html="data.more"></a>
          </div>
        </div>
      </div>

      <swiper
        class="swiper reviews__swiper"
        ref="swiperBig"
        :options="swiperOptions"
      >
        <swiper-slide
          class="reviews__slide"
          v-for="(review, k) in data.reviews"
          :key="k"
        >
          <p class="reviews__content" v-html="review.content"></p>
          <div class="reviews__info">
            <div class="reviews__details">
              <div class="reviews__author-info">
                <div class="reviews__author-pic">
                  <img :src="review.avatar" :alt="review.author" />
                </div>
                <div class="reviews__author-details">
                  <p class="reviews__author" v-html="review.author"></p>
                  <span
                    class="reviews__date"
                    v-html="
                      $dayjs(review.date)
                        .format(`D MMM YYYYY, HH:mm`)
                        .replace('+0300', '')
                    "
                  ></span>
                </div>
              </div>
              <a
                class="reviews__link"
                :href="review.link"
                target="_blank"
                v-html="review.linkText"
              ></a>
            </div>
          </div>
        </swiper-slide>
      </swiper>
      <button
        class="button swiper-reviews-prev swiper-button-prev"
        slot="button-prev"
      >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#arrow-white"></use>
        </svg>
      </button>
      <button
        class="button swiper-reviews-next swiper-button-next"
        slot="button-next"
      >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#arrow-white"></use>
        </svg>
      </button>
    </div>
  </section>
</template>

<script>
export default {
  name: "Reviews",
  components: {},
  data() {
    return {
      swiperOptions: {
        loop: true,
        centeredSlides: true,
        navigation: {
          nextEl: ".swiper-reviews-next",
          prevEl: ".swiper-reviews-prev"
        },
        breakpoints: {
          0: {
            slidesPerView: 1,
            spaceBetween: 15
          },
          768: {
            slidesPerView: "auto",
            spaceBetween: 40
          }
        },
        pagination: {
          el: ".swiper-scheme-pagination",
          clickable: true
        }
      }
    };
  },
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  methods: {},
  computed: {
    isMobile() {
      return this.$root.isMobile;
    }
  },
  mounted() {},
  beforeDestroy() {}
};
</script>
