
<template>
  <div class="callback">
    <div class="callback__head">
      <h3 class="callback__title" v-html="review.title"></h3>
      <p class="callback__text" v-html="review.text"></p>
    </div>
    <form
      id="form-review"
      action=""
      method="post"
      class="form callback__form"
    >
      <input
        class="form__input form__input--callback"
        type="text"
        placeholder="Ваше имя*"
        maxlength="32"
        v-model="form.name.value"
      />
      <input
        class="form__input form__input--callback"
        type="email"
        placeholder="E-mail*"
        maxlength="32"
        v-model="form.email.value"
      />
      <div class="form__rating tour__stars">
        <span class="tour__stars-item" v-for="(star,s) in form.stars" :key="s" @click="ChooseRating(s)">
          <i :class="{ filled: star.filled }"></i>
        </span>
      </div>
      <textarea
        class="form__input form__input--callback form__input--textarea"
        maxlength="1000"
        placeholder="Ваш отзыв*"
        v-model="form.message.value"
      />
      <div
        class="form__field-files"
        v-for="(file, f) in form.files"
        :key="f"
        :class="{
          'form__field-files--uploaded': file.value,
          'form__field-files--error': file.error,
        }"
      >
        <img :src="review.upload" alt="upload file" />
        <input
          multiple
          class="form__input"
          :id="'request-file-' + f"
          :name="'file_' + f"
          type="file"
          placeholder=""
          :ref="'file_' + f"
          @change="handleFile(file, 'file_' + f)"
        />
        <img :src="file.preview" :alt="file.value" class="preview" />
        <svg
          v-if="file.value"
          class="close"
          width="12"
          height="12"
          aria-hidden="true"
          @click="removeFile(file)"
        >
          <use xlink:href="#close"></use>
        </svg>
      </div>
      <button
        type="submit"
        class="form__submit form__submit--callback"
        v-html="review.button"
      ></button>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      form: {
        name: {
          value: "",
          label: "",
          type: "text",
          error: "",
        },
        email: {
          value: "",
          label: "",
          type: "email",
          error: "",
        },
        message: {
          value: "",
          label: "",
          error: "",
        },
        files: [
          {
            value: "",
            error: "",
            preview: "",
          },
        ],
        stars: [
          {
            filled: false
          },
          {
            filled: false
          },
          {
            filled: false
          },
          {
            filled: false
          },
          {
            filled: false
          }
        ]
      },
    };
  },
  name: "FormReview",
  computed: {},
  props: {
    review: Object,
  },
  methods: {
    ChooseRating(s) {
      this.form.stars[s].filled = false;
      this.form.stars.forEach((star, key) => {
          star.filled = false;
        if (key <= s) {
          star.filled = true;
        }
      });
    },
    handleFile(chosenFile, ref) {
      const file = this.$refs[ref][0].files[0];
      if (!file) {
        return;
      }
      const allow = [
        "jpeg",
        "jpg",
        "png",
        "mp4",
        "avi",
        "mpeg",
        "docx",
        "doc",
        "xls",
        "xlsx",
        "txt",
        "pdf",
        "msword",
        "csv",
      ];
      // const max_size = 1024 * 1024 * 10;
      let stop = true;

      for (let key in allow) {
        if (file.type.includes(allow[key])) {
          stop = false;
        }
      }
      // if (file.size > max_size) {
      //   stop = true;
      // }
      if (stop) {
        return;
      }

      this.$set(chosenFile, "value", file);
    },
    removeFile(chosenFile) {
      this.$set(chosenFile, "value", false);
    },
    async sendForm() {
      await this.$store.dispatch("fetchRequest", this);
    },
  },
};
</script>


