<template>
  <transition name="fade" v-if="data">
    <div class="modal" v-show="showModalQuestion">
      <div
        class="modal__overlay"
        @click.self="$store.commit('hideModalQuestion')"
      >
        <div class="modal__body modal__body--callback">
          <div class="modal__content">
            <div
              class="modal__close"
              @click="$store.commit('hideModalQuestion')"
            >
              <svg class="icon" width="25" height="25" aria-hidden="true">
                <use xlink:href="#close"></use>
              </svg>
            </div>
            <FormQuestion :callback="data.callback" />
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import FormQuestion from "../forms/FormQuestion.vue";
export default {
  name: "ModalQuestion",
  data() {
    return {
      messages: [],
    };
  },
  props: {},
  methods: {},
  created() {},
  computed: {
    data() {
      return this.$store.state.page.modals;
    },
    showModalQuestion() {
      return this.$store.state.showModalQuestion;
    },
  },
  components: {
    FormQuestion,
  },
};
</script>
