<template>
  <section class="regions" id="regions" v-if="data && data.enable">
    <div class="container">
      <div class="regions__inner">
        <div class="regions__titles">
          <h2 class="title" v-html="data.title"></h2>
        </div>

        <div class="regions__items">
          <div class="regions__item" v-for="(tour, j) in data.regions" :key="j">
            <a href="">
              <div class="regions__item-avatar">
                <img :src="tour.img" :alt="tour.name" />
              </div>
              <div class="regions__item-info">
                <p class="regions__item-title" v-html="tour.name"></p>
                <div class="regions__price-wrapper">
                  <span>{{ "от&nbsp;" }}</span>
                  <p v-html="+tour.price"></p>
                  <span>{{ "&nbsp;₽" }}</span>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "Regions",
  computed: {},
  data() {
    return {};
  },
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  methods: {},
  watch: {},
  mounted() {}
};
</script>
