<template>
  <div class="category__filters filters">
    <div class="filters__group">
      <label
        class="filters__group-label"
        for="filter-radio"
        v-html="data.months_title"
      ></label>
      <fieldset class="filters__group-fieldset">
        <a
          :data-url="month.href"
          :href="month.href"
          v-for="(month, m) in data.months"
          :key="m"
        >
          <label class="filters__label" @click="ChooseMonth(m)">
            <input
              type="radio"
              data-type="month"
              name="months"
              :data-id="month.id"
              :value="month.id"
            />
            <span v-html="month.name"></span>
            <span class="filters__tours">45</span>
          </label>
        </a>
      </fieldset>
    </div>
    <div class="filters__group">
      <label
        class="filters__group-label"
        for="filter-radio"
        v-html="data.days_title"
      ></label>
      <fieldset class="filters__group-fieldset">
        <a
          :data-url="day.href"
          :href="day.href"
          v-for="(day, d) in data.days"
          :key="d"
        >
          <label class="filters__label" @click="ChooseDay(d)">
            <input
              type="radio"
              data-type="day"
              name="days"
              :data-id="day.id"
              :value="day.id"
            />
            <span v-html="day.name"></span>
            <span class="filters__tours">45</span>
          </label>
        </a>
      </fieldset>
    </div>
    <div class="filters__group">
      <label class="filters__group-label" v-html="data.include_title"></label>
      <fieldset class="filters__group-fieldset">
        <label
          class="filters__label include"
          v-for="(inc, i) in data.include"
          :key="i"
          v-html="inc"
        ></label>
      </fieldset>
    </div>
    <div class="filters__group">
      <label
        class="filters__group-label"
        v-html="data.activities_title"
      ></label>
      <fieldset class="filters__group-fieldset">
        <a
          :data-url="activity.href"
          :href="activity.href"
          v-for="(activity, a) in data.activities"
          :key="a"
        >
          <label class="filters__label">
            <input
              type="checkbox"
              data-type="activity"
              name="activities"
              :data-id="activity.id"
              :value="activity.id"
            />
            <span v-html="activity.name"></span>
            <span class="filters__tours">45</span>
          </label>
        </a>
      </fieldset>
    </div>
    <div class="filters__group">
      <label
        class="filters__group-label"
        v-html="data.excursions_title"
      ></label>
      <fieldset class="filters__group-fieldset">
        <a
          :data-url="excursion.href"
          :href="excursion.href"
          v-for="(excursion, e) in data.excursions"
          :key="e"
        >
          <label class="filters__label">
            <input
              type="checkbox"
              data-type="excursion"
              name="excursions"
              :data-id="excursion.id"
              :value="excursion.id"
            />
            <span v-html="excursion.name"></span>
            <span class="filters__tours">45</span>
          </label>
        </a>
      </fieldset>
    </div>
    <div class="filters__group">
      <label class="filters__group-label" v-html="data.rivers_title"></label>
      <fieldset class="filters__group-fieldset">
        <a
          :data-url="river.href"
          :href="river.href"
          v-for="(river, r) in data.rivers"
          :key="r"
        >
          <label class="filters__label">
            <input
              type="checkbox"
              data-type="river"
              name="rivers"
              :data-id="river.id"
              :value="river.id"
            />
            <span v-html="river.name"></span>
            <span class="filters__tours">45</span>
          </label>
        </a>
      </fieldset>
    </div>
  </div>
</template>

<script>
export default {
  name: "Filters",
  data() {
    return {
      messages: [],
    };
  },
  props: {
    data: Object
  },
  methods: {
    ChooseMonth(m) {
      this.data.months[m].selected = true;
      this.data.months.forEach((month, key) => {
        if (key !== m) {
          month.selected = false;
        }
      });
    },
    ChooseDay(d) {
      this.data.days[d].selected = true;
      this.data.days.forEach((day, key) => {
        if (key !== d) {
          day.selected = false;
        }
      });
    },
    Cancel() {
      this.chosenMonths = false;
      this.chosenDays = false;
    },
  },
  created() {},
  computed: {
  },
  components: {},
};
</script>
