<template>
  <main v-if="page.id">
    <Breadcrumbs v-if="isBreadcrumbs" />

    <section class="seo-page" v-if="page.content">
      <div class="container" v-html="page.content"></div>
    </section>

    <template v-if="page.blocks">
      <template v-for="(block, b) in page.blocks">
        <ArticleHead
          :key="b"
          v-if="block.type === 'article-head'"
          :data="block"
        />
      </template>
    </template>
    <Tickets />
    <script type="application/javascript" defer>
      var UfsRailwayAppConfig = {
        isHashUrl: true,
        appDomain: "alem-tour.gdbilet.ru",
        apiDomain: "api.ufs-online.ru",
        lang: "ru",
        basename: "/bilety/",
        helpUrls: {
          ru: "https://www.ufs-partner.ru/faq/"
        },
        offer: {
          publicOfferType: "partner"
        },
        preloaderImage: {
          url: "https://www.ufs-online.ru/img/promo/3.jpg"
        }
      };
    </script>

    <script
      type="application/javascript"
      src="https://spa.ufs-online.ru/kupit-zhd-bilety/widget.js"
    ></script>
  </main>
</template>

<script>
import Breadcrumbs from "../components/Breadcrumbs.vue";
import ArticleHead from "../components/ArticleHead.vue";
import Tickets from "../components/Tickets.vue";

export default {
  components: {
    Breadcrumbs,
    ArticleHead,
    Tickets
  },
  methods: {},
  computed: {
    page() {
      return this.$store.state.page;
    },
    isBreadcrumbs() {
      if (!this.page.blocks) {
        return true;
      }
      return !["banner", "contacts"].includes(this.page.blocks[0].type);
    }
  },
  mounted() {}
};
</script>
