<template>
  <section class="call-order" v-if="data && data.enable">
    <div class="container">
      <p class="call-order__title" v-html="data.title"></p>

      <form action="#" method="post" @submit.prevent="formSend()">
        <div class="call-order__inputs">
          <div class="call-order__field">
            <input
              class="call-order__input"
              :class="{ error: form.name.error }"
              id="name"
              name="email"
              :type="form.name.type"
              size="20"
              v-model="form.name.value"
              :placeholder="form.name.placeholder"
            />
            <label class="call-order__label" for="name">
              {{ form.name.placeholder }}</label
            >
            <template v-if="form.name.error">
              <p class="call-order__error">{{ form.name.error }}</p>
            </template>
          </div>
          <div class="call-order__field">
            <input
              class="call-order__input"
              :class="{ error: form.phone.error }"
              id="phone"
              name="phone"
              :type="form.phone.type"
              size="20"
              v-model="form.phone.value"
              v-mask="'+# (###) ###-##-##'"
              :placeholder="form.phone.placeholder"
            />
            <label class="call-order__label" for="phone">
              {{ form.phone.placeholder }}
            </label>
            <template v-if="form.phone.error">
              <p class="call-order__error">{{ form.phone.error }}</p>
            </template>
          </div>

          <button
            type="submit"
            name="submit"
            class="button button--order-red "
            @click.prevent="formSend()"
            v-html="data.form_button"
          ></button>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
export default {
  name: "CallOrder",
  data() {
    return {
      form: {
        name: {
          value: "",
          placeholder: "Имя",
          type: "text",
          error: ""
        },
        phone: {
          value: "",
          label: "Телефон",
          type: "tel",
          error: "",
          placeholder: "+7 "
        }
      }
    };
  },
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  methods: {
    async formSend() {
      if (!this.$validate(this.form)) {
        return;
      }
      const data = new FormData();
      for (let key in this.form) {
        if (typeof this.form[key].value !== "undefined") {
          data.append(key, this.form[key].value);
        }
      }

      // const response = await axios
      //   .post("/lpmodal/lpmodal_send.php", data)
      //   .catch((error) => {
      //     console.log(error);
      //   });
      // if (response.data.success) {
      // this.form.success = response.data.message;
      setTimeout(() => {
        this.form = this.$cloneObj(this.form.default);
        this.form.default = this.$cloneObj(this.form);
        this.$store.state.showMessage = false;
      }, 3000);
      this.$store.state.showMessage = true;
      // }
    }
  },
  components: {},
  created() {
    this.form.default = this.$cloneObj(this.form);
  }
};
</script>

<style></style>
