
<template>
  <div class="callback">
    <div class="callback__head">
      <h3 class="callback__title" v-html="callback.title"></h3>
      <p class="callback__text" v-html="callback.text"></p>
    </div>
    <form id="form-callback" action="" method="post" class="form callback__form">
      <input
        class="form__input form__input--callback"
        type="text"
        placeholder="Ваше имя*"
        maxlength="32"
        v-model="form.name.value"
      />
      <input
        class="form__input form__input--callback"
        type="tel"
        placeholder="+7 (___) ___ - __ - __*"
        maxlength="32"
        v-model="form.phone.value"
        v-mask="'+# (###) ###-##-##'"
      />
      <button
        type="submit"
        class="form__submit form__submit--callback"
        v-html="callback.button"
      ></button>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      form: {
        name: {
          value: "",
          label: "",
          type: "text",
          error: "",
        },
        phone: {
          value: "",
          label: "",
          type: "tel",
          error: "",
        },
      },
    };
  },
  name: "Callback",
  computed: {},
  props: {
    callback: Object,
  },
  methods: {
    async sendForm() {
      if (!this.$validate(this.form)) {
        return;
      }
      const data = new FormData();
      for (let key in this.form) {
        if (typeof this.form[key].value !== "undefined") {
          data.append(key, this.form[key].value);
        }
      }
      const token = await this.$root.getRecaptchaToken("callback");
      data.append("recaptcha_response", token);
      const response = await this.$store.dispatch("fetchCallback", data);
      if (response.data.success) {
        this.success_text = response.data.message;
        //   setTimeout(() => {
        //     this.$store.commit("modalClose");
        //     this.success_text = null;
        //   }, 3000);
      }
    },
  },
};
</script>


