<template>
  <main v-if="page.id">
    <Breadcrumbs v-if="isBreadcrumbs" />

    <section class="seo-page" v-if="page.content">
      <div class="container" v-html="page.content"></div>
    </section>

    <template v-if="page.blocks">
      <template v-for="(block, b) in page.blocks">
        <Banner :key="b" v-if="block.type === 'banner'" :data="block" />
        <PopularCategories
          :key="b"
          v-if="block.type === 'popular_categories'"
          :data="block"
        />

        <Reviews :key="b" v-if="block.type === 'reviews'" :data="block" />
        <PopularTours
          :key="b"
          v-if="block.type === 'popular_tours'"
          :data="block"
        />
        <Regions :key="b" v-if="block.type === 'regions'" :data="block" />
        <Payments :key="b" v-if="block.type === 'payments'" :data="block" />
        <PricesTable :key="b" v-if="block.type === 'prices_table'" :data="block" />
      </template>
    </template>
  </main>
</template>

<script>
import Breadcrumbs from "../components/Breadcrumbs.vue";
import Banner from "../components/Banner.vue";
import Regions from "../components/Regions.vue";
import Reviews from "../components/Reviews.vue";
import PopularCategories from "../components/PopularCategories.vue";
import PopularTours from "../components/PopularTours.vue";
import Payments from "../components/Payments.vue";
import PricesTable from "../components/PricesTable.vue";

export default {
  components: {
    Breadcrumbs,
    Banner,
    Reviews,
    PopularCategories,
    PopularTours,
    Regions,
    Payments,
    PricesTable
  },
  computed: {
    page() {
      return this.$store.state.page;
    },
    isBreadcrumbs() {
      if (!this.page.blocks) {
        return true;
      }
      return !["banner", "contacts"].includes(this.page.blocks[0].type);
    }
  }
};
</script>
