<template>
  <section class="article-head" v-if="data && data.enable">
    <div class="container">
      <div class="article-head__inner" :style="style">
        <div class="article-head__contacts">
          <router-link to="/contacts">Контакты</router-link>
        </div>
        <h1 v-html="data.title"></h1>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "Covid",
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  computed: {
    style() {
      return this.data.image
        ? {
            backgroundImage: "url(" + this.data.image + ")"
          }
        : {};
    }
  }
};
</script>

