<template>
  <main v-if="page.id">
    <section class="seo-page" v-if="page.content">
      <div class="container" v-html="page.content"></div>
    </section>
    <template v-if="page.blocks">
      <template v-for="(block, b) in page.blocks">
        <Tour :key="b" v-if="block.type === 'tour'" :tour="block" />
      </template>
    </template>
  </main>
</template>

<script>
import Tour from "../components/Tour.vue";

export default {
  name: "TourPage",
  components: {
    Tour
  },
  computed: {
    page() {
      return this.$store.state.page;
    },
  },
};
</script>

