<template>
  <section
    class="popular-categories"
    id="popular-categories"
    v-if="data && data.enable"
  >
    <div class="container">
      <div class="popular-categories__inner">
        <div class="popular-categories__titles">
          <h2 class="title" v-html="data.title"></h2>

          <div class="months">
            <swiper
              class="swiper months__swiper"
              :options="swiperOptionThumbs"
              ref="swiperSmall"
            >
              <swiper-slide v-for="(month, i) in data.months" v-bind:key="i">
                <p class="months__name" v-html="month.name"></p>
              </swiper-slide>
              <div
                class="months__swiper-button months__swiper-button--categories-left"
                slot="button-prev"
              ></div>
              <div
                class="months__swiper-button months__swiper-button--categories-right"
                slot="button-next"
              ></div>
            </swiper>
          </div>
          <div class="popular-categories__more">
            <a href="#" v-html="data.more"></a>
          </div>
        </div>

        <div class="popular-categories__swiper-wrapper">
          <swiper
            class="swiper popular-categories__swiper"
            ref="swiperBig"
            :options="swiperOptionTop"
          >
            <swiper-slide
              class="popular-categories__slide"
              v-for="(slide, k) in data.slides"
              :key="k"
            >
              <div class="popular-categories__items">
                <div
                  class="regions__item"
                  v-for="(tour, j) in slide.tours"
                  :key="j"
                >
                  <a :href="tour.link">
                    <div class="regions__item-avatar">
                      <img :src="tour.img" :alt="tour.name" />
                    </div>
                    <div class="regions__item-info">
                      <p class="regions__item-title" v-html="tour.name"></p>
                      <div class="regions__price-wrapper">
                        <span>{{ "от&nbsp;" }}</span>
                        <p v-html="+tour.price"></p>
                        <span>{{ "&nbsp;₽" }}</span>
                      </div>
                    </div>
                  </a>
                </div>
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "Services",
  computed: {},
  data() {
    return {
      swiperOptionTop: {
        loop: true,
        loopedSlides: 3, // looped slides should be the same
        spaceBetween: 10,
        slidesPerView: 1,
        centeredSlides: true
      },
      swiperOptionThumbs: {
        loop: true,
        loopedSlides: 3, // looped slides should be the same
        spaceBetween: 10,
        centeredSlides: false,
        slidesPerView: 1,
        touchRatio: 0.2,
        slideToClickedSlide: true,
        navigation: {
          nextEl: ".months__swiper-button--categories-right",
          prevEl: ".months__swiper-button--categories-left"
        }
      }
    };
  },
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  methods: {},
  watch: {},
  mounted() {
    this.$nextTick(() => {
      const swiperBig = this.$refs.swiperBig.$swiper;
      const swiperSmall = this.$refs.swiperSmall.$swiper;
      swiperBig.controller.control = swiperSmall;
      swiperSmall.controller.control = swiperBig;
    });
  }
};
</script>
