<template>
  <main v-if="page.id">
    <section class="seo-page" v-if="page.content">
      <div class="container" v-html="page.content"></div>
    </section>
    <template v-if="page.blocks">
      <template v-for="(block, b) in page.blocks">
        <Banner :key="b" v-if="block.type === 'banner'" :data="block" />
        <Tours :key="b" v-if="block.type === 'tours'" :data="block"/>
        <Reviews :key="b" v-if="block.type === 'reviews'" :data="block"/>
        <ReviewSites :key="b" v-if="block.type === 'partners'" :data="block"/>
        <Texts :key="b" v-if="block.type === 'texts'" :data="block"/>
      </template>
    </template>
  </main>
</template>

<script>
import Banner from "../components/Banner.vue";
import Tours from "../components/Tours.vue";
import Reviews from '../components/Reviews.vue';
import ReviewSites from '../components/ReviewSites.vue';
import Texts from '../components/Texts.vue';

export default {
  name: "category",
  components: {
    Banner, Tours,
    Reviews, ReviewSites, Texts
  },
  computed: {
    page() {
      return this.$store.state.page;
    },
  },
  
};
</script>
