<template>
  <transition name="fade" v-if="showMessage">
    <div class="message message--shown">
      <h6 class="message__text">Ваша заявка успешно отправлена!</h6>
    </div>
  </transition>
</template>

<script>
export default {
  name: "Message",
  data() {
    return {
      messages: [],
    };
  },
  props: {},
  methods: {},
  created() {},
  computed: {
    data() {
      return this.$store.state.data.message;
    },
    showMessage() {
      return this.$store.state.showMessage;
    },
  },
};
</script>
