<template>
  <div
    class="select-modal"
    v-bind:class="{ 'select-modal--opened': isModalVisible }"
  >
    <ul class="select-modal__cities">
      <li
        class="select-modal__city"
        v-for="(item, index) in cities"
        :key="index"
        @click="chooseCity(item, index)"
        :class="{ red: item.active }"
      >
        <span v-html="item.name"></span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "SelectCity",
  props: {
    cityActive: Object,
    cities: Array,
    isModalVisible: Boolean
  },
  methods: {
    closeModal() {
      this.$emit("changeModalVisible", this.isModalVisible);
    },
    chooseCity(city, i) {
      this.$switchArray(i, this.cities, "active");
      this.closeModal();
    }
  }
};
</script>

