<template>
  <section class="footer">
    <div class="container">
      <div class="footer__top" v-if="!isTablet">
        <div class="footer__callback">
          <div class="footer__choose-city">
            <p>Телефон в&nbsp;</p>

            <span
              class="footer__city"
              v-html="cityActive.name"
              @click="openModal()"
            >
            </span>
          </div>

          <div class="footer__phones">
            <a
              :href="'tel:' + item.number"
              v-html="item.number"
              v-for="(item, j) in cityActive.phones"
              :key="j"
            ></a>
          </div>
          <SelectCity
            :cities="cities"
            :cityActive="cityActive"
            :isModalVisible="isModalVisible"
            @changeModalVisible="changeModalVisible"
          />
          <button class="button button--callback">заказать звонок</button>
        </div>
        <div class="footer__sections">
          <div
            class="footer__section"
            v-for="(section, j) in menus.footer_top.sections"
            :key="j"
          >
            <p class="footer__section-title" v-html="section.title"></p>
            <ul
              class="footer__section-links"
              v-if="section.type !== 'contacts'"
            >
              <li
                class="footer__section-link"
                v-for="(link, k) in section.links"
                :key="k"
              >
                <a :href="link.link" v-html="link.text"></a>
              </li>
            </ul>

            <ul class="footer__section-links" v-else>
              <li
                class="footer__section-link"
                v-for="(link, k) in section.links"
                :key="k"
              >
                <span v-html="link.text + '&nbsp;'"></span>
                <a :href="'tel:' + link.link" v-html="link.link"></a>
              </li>
              <li class="footer__section-link">
                <a href="#">Заказать звонок >>></a>
              </li>
              <li class="footer__section-link">
                <a href="#">Написать в телеграмм >>></a>
              </li>
            </ul>
          </div>
        </div>
      </div>

      <div v-else class="footer__top">
        <div class="footer__sections">
          <div
            class="footer__section"
            v-for="(section, j) in menus.footer_top.sections"
            :key="j"
          >
            <p class="footer__section-title" @click="toggleBlock(j)">
              {{ section.title }}

              <svg width="16" height="16" class="icon">
                <use xlink:href="#arrow-down"></use>
              </svg>
            </p>

            <slide-up-down
              v-if="section.type !== 'contacts'"
              :active="section.active"
              v-bind:class="{ show: section.show }"
            >
              <ul class="footer__section-links">
                <li
                  class="footer__section-link"
                  v-for="(link, k) in section.links"
                  :key="k"
                >
                  <a :href="link.link" v-html="link.text"></a>
                </li>
              </ul>
            </slide-up-down>

            <slide-up-down
              v-else
              :active="section.active"
              v-bind:class="{ show: section.show }"
            >
              <ul class="footer__section-links">
                <li
                  class="footer__section-link"
                  v-for="(link, k) in section.links"
                  :key="k"
                >
                  <span v-html="link.text + '&nbsp;'"></span>
                  <a :href="'tel:' + link.link" v-html="link.link"></a>
                </li>
                <li class="footer__section-link">
                  <a href="#">Заказать звонок >>></a>
                </li>
                <li class="footer__section-link">
                  <a href="https://t.me/alemtourbot"
                    >Написать в телеграмм >>></a
                  >
                </li>
              </ul>
            </slide-up-down>
          </div>
        </div>
      </div>
    </div>
    <div class="footer__bottom">
      <p>Присоединяйтесь к нам!</p>
      <div class="footer__socials">
        <div
          class="footer__social"
          v-for="item in menus.footer_bottom.socials"
          :key="item.id"
        >
          <a target="_blank" :href="item.href">
            <img :src="item.img" :alt="item.name" />
          </a>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import SelectCity from "./SelectCity.vue";
export default {
  name: "Footer",
  components: { SelectCity },
  data() {
    return {
      sectOneShow: false,
      sectSecShow: false,
      isModalVisible: false,
      active: true,
      showOptions: false
    };
  },
  computed: {
    menus() {
      return this.$store.state.menus;
    },
    cities() {
      return this.$store.state.cities;
    },
    settings() {
      return this.$store.state.settings;
    },
    isMobile() {
      return this.$root.isMobile;
    },
    isTablet() {
      return this.$root.isTablet;
    },
    showBackToTop() {
      return this.$root.window_top > 600;
    },
    cityActive() {
      if (!this.cities) {
        return false;
      }
      let cityActive = this.cities.filter((city, i) => {
        return city.active;
      });
      return cityActive[0];
    }
  },
  methods: {
    toggleBlock(i) {
      this.menus.footer_top.sections[i].active = !this.menus.footer_top
        .sections[i].active;
      this.menus.footer_top.sections.forEach((element, key) => {
        if (key !== i) {
          element.active = false;
        }
      });
    },
    openModal() {
      this.isModalVisible = true;
    },
    changeModalVisible() {
      this.isModalVisible = false;
    }
  }
};
</script>
