import аxios from 'axios';
import Vue from 'vue'
import Vuex from 'vuex'
import Cookies from 'js-cookie';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    menuShown: false,
    showMessage: false,
    showModalReview: false,
    showModalCallback: false,
    showModalSendEmail: false,
    showModalQuestion: false,
    showFilters: false,
    isScriptAdded: false,
    page: {
      blocks: {}
    },
    data: {},
    config: {},
    settings: {},
    menus: {},
    meta: {},
    user: null,
    token: Cookies.get('token'),
  },
  mutations: {
    setMenuState: (state, shown) => {
      state.menuShown = shown;
      if (shown) {
        document.body.classList.add("scroll-off");
      } else {
        document.body.classList.remove("scroll-off");
      }
    },
    setScriptAdded: (state) => {
      state.isScriptAdded = true;
    },
    showMessage: (state) => {
      state.showMessage = true;
    },
    hideMessage: (state) => {
      state.showMessage = false;
    },
    showModalReview: (state) => {
      state.showModalReview = true;
    },
    hideModalReview: (state) => {
      state.showModalReview = false;
    },
    showModalCallback: (state) => {
      state.showModalCallback = true;
    },
    hideModalCallback: (state) => {
      state.showModalCallback = false;
    },
    showModalQuestion: (state) => {
      state.showModalQuestion = true;
    },
    hideModalQuestion: (state) => {
      state.showModalQuestion = false;
    },
    showModalSendEmail: (state) => {
      state.showModalSendEmail = true;
    },
    hideModalSendEmail: (state) => {
      state.showModalSendEmail = false;
    },


    setData(state, payload) {
      for (let key in payload) {
        Vue.set(state, key, payload[key]);
      }
    },
    saveToken(state, {
      token,
      remember
    }) {
      state.token = token;
      Cookies.set('token', token, {
        expires: remember ? 365 : null
      })
    },
    fetchUserSuccess(state, {
      user
    }) {
      state.user = user;
    },
    fetchUserFail(state) {
      state.token = null;
      Cookies.remove('token');
    },
    logout(state) {
      state.user = null;
      state.token = null;
      Cookies.remove('token');
    },
    updateUser(state, user) {
      state.user = user;
    },
  },
  actions: {
    saveToken({
      commit,
      dispatch
    }, payload) {
      commit('saveToken', payload)
    },
    async fetchUser({
      commit
    }) {
      try {
        const {
          data
        } = await axios.get('/api/user')

        commit('fetchUserSuccess', {
          user: data
        })
      } catch (e) {
        commit('fetchUserFail')
      }
    },
    updateUser({
      commit
    }, payload) {
      commit('updateUser', payload)
    },
    async logout({
      commit
    }) {
      try {
        await axios.post('/api/logout')
      } catch (e) {}

      commit('logout')
    },
    async fetchOauthUrl(ctx, {
      provider
    }) {
      const {
        data
      } = await axios.post(`/api/oauth/${provider}`)

      return data.url
    }
  },
  getters: {}
})

export default store;
