<template>
  <transition name="fade" v-if="showModalReview">
    <div
        class="modal__overlay"
        @click.self="$store.commit('hideModalReview')"
      >
        <div class="modal__body modal__body--review">
          <div class="modal__content">
            <div
              class="modal__close"
              @click="$store.commit('hideModalReview')"
            >
              <svg class="icon" width="25" height="25" aria-hidden="true">
                <use xlink:href="#close"></use>
              </svg>
            </div>
            <FormReview :review="data.review" />
          </div>
        </div>
      </div>
  </transition>
</template>

<script>
import FormReview from "../forms/FormReview.vue";
export default {
  name: "ModalReview",
  data() {
    return {
      messages: [],
    };
  },
  props: {},
  methods: {},
  created() {},
  computed: {
    data() {
      return this.$store.state.page.modals;
    },
    showModalReview() {
      return this.$store.state.showModalReview;
    },
  },
  components: {
    FormReview,
  },
};
</script>
