    ></script>
  </main>
</template>

<script>
import Breadcrumbs from "../components/Breadcrumbs.vue";
import ArticleHead from "../components/ArticleHead.vue";
import Insurance from "../components/Insurance.vue";

export default {
  components: {
    Breadcrumbs,
    ArticleHead,
    Insurance
  },
  computed: {
    page() {
      return this.$store.state.page;
    },
    isBreadcrumbs() {
      if (!this.page.blocks) {
        return true;
      }
      return !["banner", "contacts"].includes(this.page.blocks[0].type);
    }
  }
};
</script>
