import Vue from "vue";
import VueRouter from "vue-router";
import store from "~/store";
import Meta from "vue-meta";
import {
  sync
} from "vuex-router-sync";
import axios from "axios";

import Page from "../views/Page.vue";
import TourPage from "../views/TourPage.vue";
import Category from "../views/Category.vue";
import Contacts from "../views/Contacts.vue";
import Covid from "../views/Covid.vue";
import Tickets from "../views/Tickets.vue";
import About from "../views/About.vue";
import Insurance from "../views/Insurance.vue";
import Podmoskovie from "../views/Podmoskovie.vue";

Vue.use(VueRouter);
Vue.use(Meta);

const routes = [{
    path: "/tour",
    name: "TourPage",
    component: TourPage
  },
  {
    path: "/category",
    name: "Category",
    component: Category
  },
  {
    path: "/contacts",
    name: "Contacts",
    component: Contacts
  },
  {
    path: "/covid",
    name: "Covid",
    component: Covid
  },
  {
    path: "/tickets",
    name: "Tickets",
    component: Tickets
  },
  {
    path: "/",
    name: "Page",
    component: Page
  },
  {
    path: "/about",
    name: "About",
    component: About
  },
  {
    path: "/insurance",
    name: "Insurance",
    component: Insurance
  },
  {
    path: "/podmoskovie",
    name: "Podmoskovie",
    component: Podmoskovie
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0,
      behavior: "smooth"
    };
  }
});

// The middleware for every page of the application.
const globalMiddleware = ["check-auth"];

// Load middleware modules dynamically.
const routeMiddleware = resolveMiddleware(
  require.context("~/middleware", false, /.*\.js$/)
);

function hasQueryParams(route) {
  return Object.keys(route.query).length;
}

/**
 * Global router guard.
 *
 * @param {Route} to
 * @param {Route} from
 * @param {Function} next
 */
async function beforeEach(to, from, next) {
  let components = []

  try {
    // Get the matched components and resolve them.
    components = await resolveComponents(
      router.getMatchedComponents({
        ...to
      })
    )
  } catch (error) {
    if (/^Loading( CSS)? chunk (\d)+ failed\./.test(error.message)) {
      window.location.reload(true)
      return
    }
  }

  if (components.length === 0) {
    return next()
  }

  if (encodeURI(from.path) != to.path && from.name !== null) {
    const response = await axios.get(to.path + '?ajax=true').catch(function (error) {
      console.log(error);
      return error.response;
    });

    store.commit("setData", response.data.alem);

    document.title = store.state.meta.title;
    document.querySelector('head meta[name="description"]').setAttribute('content', store.state.meta.description)
    document.querySelector('head meta[name="keywords"]').setAttribute('content', store.state.meta.keywords)
  }

  // Get the middleware for all the matched components.
  const middleware = getMiddleware(components)

  // Call each middleware.
  callMiddleware(middleware, to, from, (...args) => {
    next(...args)
  })

  if (!hasQueryParams(to) && hasQueryParams(from)) {
    next({
      name: to.name,
      query: from.query
    });
  }

  if (store.state.statusCode === 404) {
    next({
      name: 'Error404',
      params: [to.path],
      replace: true
    });
  }
}

/**
 * Global after hook.
 *
 * @param {Route} to
 * @param {Route} from
 * @param {Function} next
 */
async function afterEach(to, from, next) {
  await router.app.$nextTick();

  store.commit("setMenuState", false);
}

/**
 * Call each middleware.
 *
 * @param {Array} middleware
 * @param {Route} to
 * @param {Route} from
 * @param {Function} next
 */
function callMiddleware(middleware, to, from, next) {
  const stack = middleware.reverse();

  const _next = (...args) => {
    // Stop if "_next" was called with an argument or the stack is empty.
    if (args.length > 0 || stack.length === 0) {
      return next(...args);
    }

    const {
      middleware,
      params
    } = parseMiddleware(stack.pop());

    if (typeof middleware === "function") {
      middleware(to, from, _next, params);
    } else if (routeMiddleware[middleware]) {
      routeMiddleware[middleware](to, from, _next, params);
    } else {
      throw Error(`Undefined middleware [${middleware}]`);
    }
  };

  _next();
}

/**
 * @param  {String|Function} middleware
 * @return {Object}
 */
function parseMiddleware(middleware) {
  if (typeof middleware === "function") {
    return {
      middleware
    };
  }

  const [name, params] = middleware.split(":");

  return {
    middleware: name,
    params
  };
}

/**
 * Resolve async components.
 *
 * @param  {Array} components
 * @return {Array}
 */
function resolveComponents(components) {
  return Promise.all(
    components.map(component => {
      return typeof component === "function" ? component() : component;
    })
  );
}

/**
 * Merge the the global middleware with the components middleware.
 *
 * @param  {Array} components
 * @return {Array}
 */
function getMiddleware(components) {
  const middleware = [...globalMiddleware];

  components
    .filter(c => c.middleware)
    .forEach(component => {
      if (Array.isArray(component.middleware)) {
        middleware.push(...component.middleware);
      } else {
        middleware.push(component.middleware);
      }
    });

  return middleware;
}

/**
 * @param  {Object} requireContext
 * @return {Object}
 */
function resolveMiddleware(requireContext) {
  return requireContext
    .keys()
    .map(file => [file.replace(/(^.\/)|(\.js$)/g, ""), requireContext(file)])
    .reduce(
      (guards, [name, guard]) => ({
        ...guards,
        [name]: guard.default
      }), {}
    );
}

if (typeof context === "undefined") {
  // client only
  router.beforeEach(beforeEach);
  router.afterEach(afterEach);
} else {
  store.commit("hideLoader");
}

sync(store, router);

export default router;
