<template>
  <section class="prices-table" id="prices-table" v-if="data && data.enable">
    <div class="container">
      <div class="prices-table__title">
        <svg class="icon" width="38" height="38" aria-hidden="true">
          <use xlink:href="#calendar"></use>
        </svg>
        <h4 v-html="data.title"></h4>
      </div>

      <div class="prices-table__items">
        <div
          class="prices-table__item"
          v-for="(month, i) in data.months"
          :key="i"
        >
          <a
            target="_blank"
            :href="month.link"
            class="prices-table__name"
            v-html="month.name"
          ></a>
          <p
            class="prices-table__price"
            v-html="'от ' + month.price + ' Р'"
          ></p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "PricesTable",
  props: {
    data: {
      type: Object,
      default: null
    }
  }
};
</script>

<style></style>
