<template>
  <section class="category" v-if="data">
    <div class="container">
      <div class="category__chosen">
        <h2
          class="category__chosen-title"
          v-if="this.chosenMonths || this.chosenDays"
        >
          <span v-html="data.title"></span
          ><span v-if="this.chosenMonths">{{
            data.insert.replace("%s", this.chosenMonths.name_changed + " ")
          }}</span
          ><span v-if="this.chosenDays">{{
            data.insert.replace("%s", this.chosenDays.name_changed + " ")
          }}</span>
        </h2>
        <div class="category__chosen-items">
          <button
            class="category__chosen-item"
            @click="Cancel()"
            v-if="chosenMonths"
          >
            <span v-html="this.chosenMonths.name"></span>
            <svg width="16" height="16">
              <use xlink:href="#close"></use>
            </svg>
          </button>
          <button
            class="category__chosen-item"
            @click="Cancel()"
            v-if="chosenDays"
          >
            <span v-html="this.chosenDays.name"></span>
            <svg width="16" height="16">
              <use xlink:href="#close"></use>
            </svg>
          </button>
        </div>
      </div>
      <div class="category__inner">
        <div class="category__sidebar">
          <Filters :data="data" v-if="!$root.isMobile" />
          <div class="category__links">
            <h4 class="category__links-title" v-html="data.links_title"></h4>
            <router-link
              class="category__links-item"
              :class="{ 'category__links-item--active': link.active }"
              v-for="(link, l) in data.links"
              :key="l"
              :to="link.href"
              v-html="link.name"
            ></router-link>
          </div>
          <div class="category__info infoblock">
            <div class="infoblock__head reestr">
              <div class="reestr__logo">
                <img :src="data.infoblock.head.logo" alt="reestr logo" />
              </div>
              <div class="reestr__texts">
                <a :href="data.infoblock.head.link" target="_blank"
                  >Мы в реестре туроператоров<br />
                  &gt;&gt;&gt;</a
                >
                <p class="bold">ООО "АЛЕМ-ТУР"</p>
                <p>РТО 000877</p>
              </div>
            </div>
            <div class="infoblock__item infoblock__item--link">
              <hr />
              <a
                :href="data.infoblock.phone.link"
                v-html="data.infoblock.phone.text"
              ></a>
            </div>
            <div class="infoblock__item infoblock__item--link">
              <hr />
              <span
                v-html="data.infoblock.call_request"
                @click="showModalCallback()"
              ></span>
            </div>
            <div
              class="infoblock__item infoblock__item--slide"
              v-for="(slide, s) in data.infoblock.slides"
              :key="s"
            >
              <hr />
              <div
                class="infoblock__item-title"
                @click="toggleSlides(s)"
                :class="{ 'infoblock__item-title--active': slide.show }"
              >
                <span v-html="slide.title"></span>
                <svg width="14" height="8">
                  <use xlink:href="#chevron"></use>
                </svg>
              </div>
              <slide-up-down :active="slide.show" :duration="500"
                ><span v-html="slide.text" class="infoblock__item-text"></span>
              </slide-up-down>
            </div>
            <div class="infoblock__item infoblock__item--footer">
              <hr />
              <span v-html="data.infoblock.footer"></span>
            </div>
          </div>
        </div>
        <div class="category__tours">
          <div class="category__sort">
            <div class="category__sort-title" v-html="data.sort_title"></div>
            <div
              class="category__sort-item"
              v-for="(item, i) in data.sort"
              :key="i"
              :data-type="item.type"
              v-html="item.name"
              @click="Sort(i)"
              :class="{
                'category__sort-item--active': item.active,
                'category__sort-item--asc': item.asc,
              }"
            ></div>
          </div>
          <button v-if="$root.isMobile" @click="showFilters()">Фильтр</button>
          <ToursList :data="data" />
        </div>
      </div>
      <div class="category__button">
        <button class="button button--showmore" v-html="data.showmore"></button>
      </div>
    </div>
    <transition name="fade" v-if="$root.isMobile">
      <div class="modal" v-show="filtersShown">
        <div class="modal__overlay" @click.self="hideFilters">
          <div class="modal__body">
            <div class="modal__header">
              <h4 class="modal__title">Фильтр</h4>
              <div class="modal__close" @click="hideFilters">
                <svg class="icon" width="25" height="25" aria-hidden="true">
                  <use xlink:href="#close"></use>
                </svg>
              </div>
            </div>
            <div class="modal__content">
              <Filters :data="data" />
            </div>
          </div>
        </div>
      </div>
    </transition>
  </section>
</template>

<script>
import Filters from "./Filters.vue";
import ToursList from "./ToursList.vue";
export default {
  name: "Tours",
  computed: {
    // tours() {
    //   return this.$store.state.tours_list;
    // },
    filtersShown() {
      return this.$store.state.showFilters;
    },
    chosenOptions: function () {
      let chosen = [];
      chosen.push(this.chosenMonths, this.chosenDays);
      return chosen;
    },
    chosenMonths: function () {
      let chosen_month = [];
      this.data.months.forEach((month) => {
        if (month.selected) {
          chosen_month.push(month);
        }
      });
      return chosen_month[0];
    },
    chosenDays: function () {
      let chosen_day = [];
      this.data.days.forEach((day) => {
        if (day.selected) {
          chosen_day.push(day);
        }
      });
      return chosen_day[0];
    },
  },
  props: {
    data: Object,
  },
  components: {
    ToursList,
    Filters,
  },
  methods: {
    Sort(i) {
      this.data.sort[i].active = false;
      this.data.sort.forEach((item, key) => {
        if (key !== i) {
          item.active = false;
          item.asc = false;
        } else {
          item.active = true;
          item.asc = !item.asc;
        }
      });
    },
    toggleSlides(s) {
      this.data.infoblock.slides[s].show = !this.data.infoblock.slides[s].show;
    },
    showModalCallback() {
      this.$store.state.showModalCallback = true;
    },
    showFilters() {
      this.$store.state.showFilters = true;
    },
    hideFilters() {
      this.$store.state.showFilters = false;
    },
  },
};
</script>
