import Vue from "vue";
import App from "./layouts/App.vue";
import store from "./store";
import router from "./router";
import Helpers from "./utils/Helpers";
import VueMask from "v-mask";
import SlideUpDown from "vue-slide-up-down";
// import VueImg from "v-img";
import VueLazyload from "vue-lazyload";
import Bowser from "bowser";
import axios from "axios";
import vSelect from "vue-select";
import "vue-select/dist/vue-select.css";
import VueAwesomeSwiper from "vue-awesome-swiper";
import "swiper/css/swiper.css";
import Dayjs from "vue-dayjs";
import { VueMasonryPlugin } from "vue-masonry";
import VueImg from 'v-img';

Vue.use(VueAwesomeSwiper /* { default options with global component } */);
// Vue.use(VueImg);
Vue.use(VueMask);
Vue.use(Dayjs);
Vue.use(Helpers);
Vue.component("slide-up-down", SlideUpDown);
Vue.component("v-select", vSelect);
Vue.use(VueMasonryPlugin);
Vue.use(VueImg);

Vue.use(VueLazyload);

const appVue = new Vue({
  store,
  router,
  data() {
    return {
      window_top: 0,
      header_top: 0,
      header_height: 36,
      header_height_mob: 65,
      tabletBreakpoint: 1023,
      mobileBreakpoint: 767,
      width: null,
      height: null,
      timeout_stick: null,
      ym_counter: null,
      recaptcha_key: null,
      bowser: {}
    };
  },
  watch: {
    window_top: {
      handler(new_v, old_v) {
        let header_top;

        if (!this.isMobile) {
          if (old_v < new_v && new_v > this.header_height) {
            header_top = -this.header_height;
          } else {
            header_top = 0;
          }
        } else {
          if (old_v < new_v && new_v > this.header_height_mob) {
            header_top = -this.header_height_mob;
          } else {
            header_top = 0;
          }
        }

        if (this.timeout_stick !== null) {
          return;
        }
        this.timeout_stick = setTimeout(() => {
          this.header_top = header_top;
          this.timeout_stick = null;
        }, 100);
      },
      deep: true
    }
  },
  computed: {
    isTablet() {
      return this.width <= this.tabletBreakpoint;
    },
    isMobile() {
      return this.width <= this.mobileBreakpoint;
    }
  },
  async created() {
    this.width = window.innerWidth;
    this.height = window.innerHeight;

    this.bowser = Bowser.parse(window.navigator.userAgent);

    const data = window.alem;

    this.$store.commit("setData", data);

    this.addListeners();
  },
  mounted() {},
  methods: {
    addListeners() {
      window.addEventListener("scroll", this.onScroll);
      window.addEventListener("resize", this.onResize);
      // window.onload = () => {};
    },
    onScroll(e) {
      this.window_top = window.pageYOffset;
    },
    onResize() {
      this.width = window.innerWidth;
      this.height = window.innerHeight;
    },
    reachGoal(goal) {
      if (typeof ym !== "undefined") {
        ym(this.ym_counter, "reachGoal", goal);
        console.log("reachGoal: " + goal);
      }
    },
    getRecaptchaToken(action) {
      return new Promise((resolve, reject) => {
        if (typeof grecaptcha === "undefined") {
          return resolve(false);
        }
        grecaptcha.ready(() => {
          grecaptcha
            .execute(this.recaptcha_key, {
              action: action
            })
            .then(
              token => {
                return resolve(token);
              },
              error => {
                return resolve(false);
              }
            );
        });
      });
    },
    async getDataAjax(route = "/", config = {}) {
      const response = await axios.get(route, config).catch(function(error) {
        return error.response;
      });

      return Promise.resolve(response.data);
    }
  },
  render: h => h(App)
});

appVue.$mount("#app");
