<template>
  <section class="tour-page" v-if="tour">
    <div class="container">
      <div class="tour-page__wrapper">
        <Breadcrumbs :tour="tour" />
        <div class="tour-page__inner">
          <div class="tour-page__main">
            <div v-if="tour.images.length < 2" class="tour-page__images">
              <div
                v-for="(image, i) in tour.images"
                class="tour-page__image"
                :key="i"
              >
                <img
                  v-img="{
                    group: 'tour-images' + tour.id,
                    src: image,
                  }"
                  v-lazy="image"
                  :src="image"
                  :alt="tour.title"
                />
              </div>
            </div>
            <div v-else class="tour-page__images">
              <swiper
                class="swiper tour-page__swiper"
                ref="swiperTop"
                :options="swiperOptionTop"
              >
                <swiper-slide
                  class="tour-page__swiper-item"
                  v-for="(image, i) in tour.images"
                  :key="i"
                >
                  <div class="tour-page__image">
                    <img
                      v-img="{
                        group: 'tour-images' + tour.id,
                        src: image,
                      }"
                      v-lazy="image"
                      :src="image"
                      :alt="tour.title"
                    />
                  </div>
                </swiper-slide>
              </swiper>
              <swiper
                class="swiper tour-page__thumbs"
                :options="swiperOptionThumbs"
                ref="swiperThumbs"
              >
                <swiper-slide
                  v-for="(image, i) in tour.images"
                  :key="i"
                  class="swiper tour-page__thumbs-item"
                >
                  <div class="tour-page__image tour-page__image--thumb">
                    <img v-lazy="image" :src="image" :alt="tour.title" />
                  </div>
                </swiper-slide>
                <div
                  class="
                    tour-page__swiper-button tour-page__swiper-button--left
                  "
                  slot="button-prev"
                >
                  <svg width="15" height="28">
                    <use xlink:href="#arrow-swiper"></use>
                  </svg>
                </div>
                <div
                  class="
                    tour-page__swiper-button tour-page__swiper-button--right
                  "
                  slot="button-next"
                >
                  <svg width="15" height="28">
                    <use xlink:href="#arrow-swiper"></use>
                  </svg>
                </div>
              </swiper>
            </div>
            <div v-if="!this.$root.isTablet" class="tour-page__tabs-wrapper">
              <nav v-if="window_top < 651" class="tour-page__tabs">
                <template v-for="(tab, t) in tour.tabs">
                  <a
                    v-if="tab.link"
                    :key="t"
                    class="tour-page__tabs-item"
                    href="#"
                    @click.prevent="$scrollTo(tab.link, 100)"
                    v-html="tab.name"
                  ></a>
                </template>
              </nav>
              <nav
                v-else
                class="tour-page__tabs tour-page__tabs--fixed"
                :style="{ top: header + 106 + 'px' }"
              >
                <template v-for="(tab, t) in tour.tabs">
                  <a
                    v-if="tab.link"
                    :key="t"
                    class="tour-page__tabs-item"
                    href="#"
                    @click.prevent="$scrollTo(tab.link, 100)"
                    v-html="tab.name"
                  ></a>
                </template>
              </nav>
            </div>
            <div
              class="tour-page__short"
              v-for="(desc, d) in tour.desc_short"
              :key="d"
              v-html="desc"
            ></div>
            <div class="tour-page__desc" id="description">
              <div
                class="tour-page__desc-block"
                v-for="(desc, d) in tour.desc"
                :key="d"
              >
                <h5 class="tour-page__desc-title">
                  <span v-html="desc.name"></span>
                  <span v-html="desc.title"></span>
                </h5>
                <div
                  class="tour-page__desc-content"
                  v-for="(item, i) in desc.content"
                  :key="i"
                >
                  <p
                    v-if="item.subtitle"
                    class="bold"
                    v-html="item.subtitle"
                  ></p>
                  <div
                    :key="i"
                    v-for="(item, i) in item.text"
                    v-html="item"
                  ></div>
                  <img v-if="item.image" :src="item.image" :alt="desc.title" />
                </div>
              </div>
            </div>
            <a
              class="tour-page__review-link"
              href="#"
              @click.prevent="showModalReview()"
              >Оставить отзыв</a
            >
            <div class="tour-page__lists">
              <div class="tour-page__list">
                <h5 class="tour-page__list-title">В стоимость входит</h5>
                <div
                  class="tour-page__list-wrapper"
                  v-for="(block, b) in tour.included"
                  :key="b"
                >
                  <h6
                    class="tour-page__list-subtitle"
                    v-if="block.subtitle"
                    v-html="block.subtitle"
                  ></h6>
                  <ul class="tour-page__list-ul">
                    <li
                      class="tour-page__list-li tour-page__list-li--included"
                      v-for="(item, i) in block.list"
                      :key="i"
                      v-html="item"
                    ></li>
                  </ul>
                </div>
              </div>
              <div class="tour-page__list">
                <h5 class="tour-page__list-title">В стоимость не входит</h5>
                <div
                  class="tour-page__list-wrapper"
                  v-for="(block, b) in tour.excluded"
                  :key="b"
                >
                  <h6
                    class="tour-page__list-subtitle"
                    v-if="block.subtitle"
                    v-html="block.subtitle"
                  ></h6>
                  <ul class="tour-page__list-ul">
                    <li
                      class="tour-page__list-li tour-page__list-li--excluded"
                      v-for="(item, i) in block.list"
                      :key="i"
                      v-html="item"
                    ></li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="tour-page__accommodation" id="accommodation">
              <h5>
                <span
                  class="tour-page__accommodation-title"
                  v-html="tour.accommodation.title"
                ></span>
                <span
                  class="tour-page__accommodation-title-desc"
                  v-html="tour.accommodation.title_desc"
                ></span>
              </h5>
              <p class="bold" v-html="tour.accommodation.subtitle"></p>
              <p class="bold" v-html="tour.accommodation.sales"></p>
              <div
                class="tour-page__accommodation-sales"
                v-for="(list, l) in tour.accommodation.sales_lists"
                :key="l"
              >
                <p class="" v-html="list.title"></p>
                <ul>
                  <li v-for="(li, l) in list.list" :key="l" v-html="li"></li>
                </ul>
              </div>
              <div class="tour-page__accommodation-tables">
                <div
                  v-for="(table, t) in tour.accommodation.tables"
                  :key="t"
                  :id="table.id"
                  class="tour-page__accommodation-table"
                >
                  <h4
                    v-html="table.title"
                    class="tour-page__accommodation-table-title"
                  ></h4>
                  <div class="tour-page__accommodation-table-content">
                    <div
                      v-for="(item, i) in table.content"
                      :key="i"
                      class="tour-page__accommodation-table-row"
                    >
                      <div class="tour-page__accommodation-table-subtitle">
                        <span v-html="item.subtitle.name"></span>
                        <span v-html="item.subtitle.value"></span>
                      </div>
                      <div
                        v-for="(row, r) in item.rows"
                        :key="r"
                        class="tour-page__accommodation-table-row"
                      >
                        <span v-html="row.amount"></span>
                        <span v-html="row.name"></span>
                        <span v-html="row.value"></span>
                      </div>
                    </div>
                  </div>
                  <a href="#" @click="showModalCallback()">Расчитать стоимость</a>
                  <a href="#" @click="showModalCallback()">Забронировать</a>
                </div>
              </div>
            </div>
          </div>
          <div class="tour-page__sidebar sidebar">
            <div class="tour-page__summary">
              <div class="tour-page__price">
                <span class="tour-page__price-from">от</span>
                <span class="tour-page__price-price" v-html="tour.price"></span>
                <span class="tour-page__price-currency">₽</span>
                <span class="tour-page__price-person">цена за человека</span>
              </div>
              <div class="tour-page__days">
                <span v-html="tour.days"></span>
              </div>
              <div class="tour-page__next">
                <span class="tour-page__next-title">Ближайшие заезды</span>
                <div class="tour-page__next-items">
                  <template v-for="(next, n) in tour.nexts">
                    <div :key="n" v-if="n < 4">
                      <div class="tour-page__next-item">
                        <svg width="6" height="11">
                          <use xlink:href="#triangle"></use></svg
                        ><span v-html="next"></span>
                      </div>
                    </div>
                    <slide-up-down
                      :key="n"
                      v-else
                      :active="tour.nexts_shown"
                      :duration="500"
                    >
                      <div class="tour-page__next-item">
                        <svg width="6" height="11">
                          <use xlink:href="#triangle"></use></svg
                        ><span v-html="next"></span>
                      </div>
                    </slide-up-down>
                  </template>
                  <a
                    class="tour-page__next-link"
                    v-if="tour.nexts_shown"
                    href="#"
                    @click.prevent="ToggleNexts()"
                    ><span>Скрыть все варианты</span></a
                  >
                  <a
                    class="tour-page__next-link"
                    v-else
                    href="#"
                    @click.prevent="ToggleNexts()"
                    ><span>Показать все варианты</span></a
                  >
                </div>
              </div>
            </div>
            <div class="tour-page__list" id="included">
              <h5 class="tour-page__list-title">В стоимость входит</h5>
              <div
                class="tour-page__list-wrapper"
                v-for="(block, b) in tour.included"
                :key="b"
              >
                <h6
                  class="tour-page__list-subtitle"
                  v-if="block.subtitle"
                  v-html="block.subtitle"
                ></h6>
                <ul class="tour-page__list-ul">
                  <li
                    class="tour-page__list-li tour-page__list-li--included"
                    v-for="(item, i) in block.list"
                    :key="i"
                    v-html="item"
                  ></li>
                </ul>
              </div>
            </div>
            <div class="tour-page__list">
              <h5 class="tour-page__list-title">В стоимость не входит</h5>
              <div
                class="tour-page__list-wrapper"
                v-for="(block, b) in tour.excluded"
                :key="b"
              >
                <h6
                  class="tour-page__list-subtitle"
                  v-if="block.subtitle"
                  v-html="block.subtitle"
                ></h6>
                <ul class="tour-page__list-ul">
                  <li
                    class="tour-page__list-li tour-page__list-li--excluded"
                    v-for="(item, i) in block.list"
                    :key="i"
                    v-html="item"
                  ></li>
                </ul>
              </div>
            </div>
            <FormQuestion :question="tour.question" />
            <FormCallback :callback="tour.callback" />
            <FormBooking :booking="tour.booking" />
            <div class="tour-page__infoblock infoblock">
              <div class="infoblock__head reestr">
                <div class="reestr__logo">
                  <img :src="tour.infoblock.head.logo" alt="reestr logo" />
                </div>
                <div class="reestr__texts">
                  <a :href="tour.infoblock.head.link" target="_blank"
                    >Мы в реестре туроператоров<br />
                    &gt;&gt;&gt;</a
                  >
                  <p class="bold">ООО "АЛЕМ-ТУР"</p>
                  <p>РТО 000877</p>
                </div>
              </div>
              <div class="infoblock__item infoblock__item--link">
                <hr />
                <a
                  :href="tour.infoblock.phone.link"
                  v-html="tour.infoblock.phone.text"
                ></a>
              </div>
              <div class="infoblock__item infoblock__item--link">
                <hr />
                <span
                  v-html="tour.infoblock.call_request"
                  @click="showModalCallback()"
                ></span>
              </div>
              <div
                class="infoblock__item infoblock__item--slide"
                v-for="(slide, s) in tour.infoblock.slides"
                :key="s"
              >
                <hr />
                <div
                  class="infoblock__item-title"
                  @click="toggleSlides(s)"
                  :class="{ 'infoblock__item-title--active': slide.show }"
                >
                  <span v-html="slide.title"></span>
                  <svg width="14" height="8">
                    <use xlink:href="#chevron"></use>
                  </svg>
                </div>
                <slide-up-down :active="slide.show" :duration="500"
                  ><span
                    v-html="slide.text"
                    class="infoblock__item-text"
                  ></span>
                </slide-up-down>
              </div>
              <div class="infoblock__item infoblock__item--footer">
                <hr />
                <span v-html="tour.infoblock.footer"></span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Breadcrumbs from "../components/Breadcrumbs.vue";
import FormBooking from "./forms/FormBooking.vue";
import FormCallback from "./forms/FormCallback.vue";
import FormQuestion from "./forms/FormQuestion.vue";
export default {
  name: "Tour",
  data() {
    return {
      swiperOptionTop: {
        loop: true,
        loopedSlides: 15, // looped slides should be the same
        spaceBetween: 10,
        slidesPerView: 1,
        centeredSlides: true,
        effect: "fade",
      },
      swiperOptionThumbs: {
        loop: true,
        loopedSlides: 15, // looped slides should be the same
        spaceBetween: 0,
        centeredSlides: false,
        slidesPerView: 4,
        touchRatio: 0.2,
        slideToClickedSlide: true,
        navigation: {
          nextEl: ".tour-page__swiper-button--right",
          prevEl: ".tour-page__swiper-button--left",
        },
      },
    };
  },
  computed: {
    window_top() {
      return this.$root.window_top;
    },
    header() {
      return this.$root.header_top;
    },
  },
  mounted() {
    this.$nextTick(() => {
      if (this.tour.images.length > 1) {
        const swiperTop = this.$refs.swiperTop.$swiper;
        const swiperThumbs = this.$refs.swiperThumbs.$swiper;
        swiperTop.controller.control = swiperThumbs;
        swiperThumbs.controller.control = swiperTop;
      }
    });
  },
  props: {
    tour: Object,
  },
  components: {
    Breadcrumbs,
    FormCallback,
    FormQuestion,
    FormBooking,
  },
  methods: {
    ToggleNexts() {
      this.$toggleItem(this.tour, "nexts_shown");
    },
    toggleSlides(s) {
      this.tour.infoblock.slides[s].show = !this.tour.infoblock.slides[s].show;
    },
    showModalQuestion() {
      this.$store.state.showModalCallback = true;
    },
    showModalCallback() {
      this.$store.state.showModalCallback = true;
    },
    showModalReview() {
      this.$store.state.showModalReview = true;
    },
  },
};
</script>
