<template>
  <div class="form" v-if="data">
    <form action="#" method="post" @submit.prevent="formSend()">
      <div class="form__inputs">
        <div class="form__inputs-wrapper">
          <div class="form__field" v-for="item in data.inputs" :key="item.id">
            <v-select
              v-model="item.options.name"
              label="name"
              :options="item.options"
              :placeholder="item.placeholder"
              :value="item.value"
            >
            </v-select>
            <label :class="[`form__label`, item.class]"> </label>
            <template v-if="form.name.error">
              <p class="form__error">{{ form.name.error }}</p>
            </template>
          </div>
        </div>

        <button
          type="submit"
          name="submit"
          class="button button--red"
          @click.prevent="formSend()"
        >
          {{ data.button }}
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Form",
  data() {
    return {
      form_success: false,
      form: {
        name: {
          value: "",
          placeholder: "Ваше имя",
          type: "text",
          error: ""
        },
        phone: {
          value: "",
          label: "Телефон",
          type: "tel",
          error: "",
          placeholder: "Номер телефона"
        }
      },
      data: {
        button: "Найти тур",
        user_icon: "#user",
        phone_icon: "#phone",
        inputs: [
          {
            placeholder: "Россия",
            class: "form__label--country",
            value: "",
            options: [
              {
                name: "Россия"
              },
              {
                name: "Украина"
              },
              {
                name: "Эстония"
              },
              {
                name: "Финляндия"
              }
            ]
          },
          {
            placeholder: "Выберите месяц",
            value: "",
            class: "form__label--calendar",
            options: [
              {
                name: "Январь"
              },
              {
                name: "Февраль"
              },
              {
                name: "Март"
              },
              {
                name: "Апрель"
              },
              {
                name: "Май"
              },
              {
                name: "Июнь"
              },
              {
                name: "Июль"
              },
              {
                name: "Август"
              },
              {
                name: "Сентябрь"
              },
              {
                name: "Октябрь"
              },
              {
                name: "Ноябрь"
              },
              {
                name: "Декабрь"
              }
            ]
          },
          {
            placeholder: "Количество дней",
            value: "",
            class: "form__label--days",
            options: [
              {
                name: "1"
              },
              {
                name: "2"
              },
              {
                name: "3"
              },
              {
                name: "4"
              },
              {
                name: "5"
              },
              {
                name: "6"
              },
              {
                name: "7"
              }
            ]
          }
        ]
      }
    };
  },
  props: {
    goal: {
      type: String,
      default: null
    }
  },
  computed: {},
  mounted() {
  },
  methods: {
    showMessage() {
      this.$store.commit("showMessage");
    },
    hideMessage() {
      this.$store.commit("hideMessage");
    },
    async formSend() {
      if (!this.$validate(this.form)) {
        return;
      }

      const data = new FormData();
      for (let key in this.form) {
        if (typeof this.form[key].value !== "undefined") {
          data.append(key, this.form[key].value);
        }
      }

      // const token = await this.$root.getRecaptchaToken("form");
      // data.append("recaptcha_response", token);

      const response = await axios
        .post("/api/request", data)
        .catch(error => {});

      if (response.data.success) {
        this.showMessage();
        setTimeout(() => {
          for (let key in this.form) {
            if (typeof this.form[key].value !== "undefined") {
              this.form[key].value = "";
            }
          }
          this.hideMessage();
        }, 3000);

        if (this.goal) {
          this.$root.reachGoal(this.goal);
        }
      }
    }
  },
  components: {},
  created() {}
};
</script>
