<template>
  <header
    id="header"
    class="header"
    :class="{ 'header--opened': $store.state.menuShown }"
    :style="{ top: header + 'px' }"
  >
    <section class="header__desktop" v-if="!isMobile">
      <div class="header__top">
        <div class="container">
          <div class="header__top-inner">
            <div class="header__choose-city">
              <p>Телефон в&nbsp;</p>
              <div class="header__city" @click="openModal()">
                <span v-html="cityActive.name"> </span>
                <svg
                  :class="{
                    'header__city-icon-rotated': isModalVisible
                  }"
                  width="14"
                  height="8"
                  aria-hidden="true"
                >
                  <use xlink:href="#arrow-select"></use>
                </svg>
              </div>
            </div>

            <div class="header__phones">
              <a
                :href="'tel:' + item.number"
                v-html="item.number"
                v-for="(item, j) in cityActive.phones"
                :key="j"
              ></a>
            </div>

            <SelectCity
              :cities="cities"
              :cityActive="cityActive"
              :isModalVisible="isModalVisible"
              @changeModalVisible="changeModalVisible"
            />
          </div>
        </div>
      </div>
      <div class="header__main">
        <div class="container">
          <div class="header__main-inner">
            <router-link to="/" href="/" class="header__logo">
              <img loading="lazy" :src="settings.logo" alt="logo" />
            </router-link>
            <ul class="header__navigation">
              <li
                v-for="(item, i) in menus.header.navigation"
                :key="i"
                class="header__navigation-item"
              >
                <router-link
                  :to="item.href"
                  class="header__navigation-link"
                  v-html="item.name"
                >
                </router-link>
              </li>
            </ul>
            <div class="header__icons-wrapper">
              <ul class="header__icons">
                <li
                  class="header__icon"
                  v-for="(item, i_d) in menus.icons"
                  :key="i_d"
                >
                  <div
                    class="header__icon-wrapper"
                    v-if="item.type !== 'search'"
                  >
                    <button class="button">
                      <svg
                        :width="item.width"
                        :height="item.height"
                        aria-hidden="true"
                        class="icon"
                      >
                        <use :xlink:href="item.icon"></use>
                      </svg>
                    </button>
                    <template v-if="item.quantity">
                      <span v-html="item.quantity"></span>
                    </template>
                  </div>

                  <div class="header__icon-wrapper" id="block-search" v-else>
                    <input
                      type="search"
                      name="search"
                      id="search"
                      class="header__search-input"
                      :class="{
                        'header__search-input--visible': isSearchVisible
                      }"
                      :placeholder="placeholder"
                    />
                    <button class="button" @click="toggleSearch()">
                      <svg
                        :width="item.width"
                        :height="item.height"
                        aria-hidden="true"
                        class="icon"
                      >
                        <use :xlink:href="item.icon"></use>
                      </svg>
                    </button>
                    <template v-if="item.quantity">
                      <span v-html="item.quantity"></span>
                    </template>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section
      class="header__mobile"
      :class="{ 'header__mobile--opened': $store.state.menuShown }"
      v-else
    >
      <div class="container">
        <div class="header__main-inner">
          <router-link to="/" href="/" class="header__logo">
            <img loading="lazy" :src="settings.logo" alt="logo" />
          </router-link>
          <button class="header__button" @click.prevent="menuToggle()">
            <svg
              class="icon"
              width="16"
              height="16"
              aria-hidden="true"
              v-if="!$store.state.menuShown"
            >
              <use xlink:href="#burger"></use>
            </svg>

            <svg
              class="icon"
              width="12"
              height="12"
              aria-hidden="true"
              v-if="$store.state.menuShown"
            >
              <use xlink:href="#close-menu"></use>
            </svg>
            <span>Меню</span>
          </button>
        </div>
      </div>
      <transition name="slide-left">
        <div class="header__mobile-wrapper" v-if="$store.state.menuShown">
          <div class="container">
            <Search />
            <div class="header__top-inner">
              <div class="header__choose-city">
                <p>Телефон в&nbsp;</p>
                <div class="header__city" @click="openModal()">
                  <span v-html="cityActive.name"> </span>
                </div>
              </div>

              <div class="header__phones">
                <a
                  :href="'tel:' + item.number"
                  v-for="(item, j) in cityActive.phones"
                  :key="j"
                >
                  <svg class="icon" width="10" height="14" aria-hidden="true">
                    <use xlink:href="#phone"></use>
                  </svg>
                  {{ item.number.replace(/-/g, " ") }}</a
                >
              </div>

              <SelectCity
                :cities="cities"
                :cityActive="cityActive"
                :isModalVisible="isModalVisible"
                @changeModalVisible="changeModalVisible"
              />
            </div>
            <ul class="header__navigation">
              <li
                v-for="(item, i) in menus.header.navigation"
                :key="i"
                class="header__navigation-item"
              >
                <router-link
                  :to="item.href"
                  class="header__navigation-link"
                  v-html="item.name"
                >
                </router-link>
              </li>
            </ul>
            <div class="category category--header">
              <h4 class="category__links-title">
                Все туры по
                <span
                  v-for="item in menus.header.dropdown_links.filter(elem => {
                    if (elem.active === true) {
                      return elem;
                    }
                  })"
                  :key="item.id"
                >
                  <a :href="item.link" v-html="item.text"></a>
                </span>

                <ul class="category__dropdown-links">
                  <li
                    class="category__dropdown-link"
                    v-for="(item, k) in menus.header.dropdown_links"
                    :key="k"
                  >
                    <a
                      :href="item.link"
                      v-html="item.text"
                      @click="$switchActive(k, menus.header.dropdown_links)"
                    ></a>
                  </li>
                </ul>
              </h4>

              <div
                class="category__links"
                v-for="(item, j) in menus.header.tours"
                :key="j"
              >
                <p class="category__title" v-html="item.title"></p>
                <router-link
                  class="category__links-item"
                  :class="{ 'category__links-item--active': link.active }"
                  v-for="(link, l) in item.links"
                  :key="l"
                  :to="link.href"
                  v-html="link.name"
                ></router-link>
              </div>
              <button class="button button--watched">
                <svg width="25" height="15" aria-hidden="true">
                  <use xlink:href="#watched"></use>
                </svg>
                Просмотренное
              </button>
            </div>
          </div>
        </div>
      </transition>
    </section>
  </header>
</template>

<script>
import SelectCity from "./SelectCity.vue";
import Search from "./Search.vue";
export default {
  name: "Header",
  components: {
    SelectCity,
    Search
  },
  data() {
    return {
      isModalVisible: false,
      isSearchVisible: false,
      placeholder: "Поиск по сайту"
    };
  },
  computed: {
    menus() {
      return this.$store.state.menus;
    },
    cities() {
      return this.$store.state.cities;
    },
    settings() {
      return this.$store.state.settings;
    },
    isMobile() {
      return this.$root.isMobile;
    },
    header() {
      return this.$root.header_top;
    },
    cityActive() {
      if (!this.cities) {
        return false;
      }
      let cityActive = this.cities.filter((city, i) => {
        return city.active;
      });
      return cityActive[0];
    }
  },
  mounted() {
    if (this.isMobile) {
      document.addEventListener("keyup", evt => {
        if (evt.keyCode === 27) {
          this.menuClose();
          this.closeSearch(evt);
        }
      });
    }
    window.addEventListener("resize", this.menuClose);

  },
  watch: {},
  methods: {
    menuClose() {
      this.$store.commit("setMenuState", false);
    },
    menuToggle() {
      this.$store.commit("setMenuState", !this.$store.state.menuShown);
    },
    openModal() {
      this.isModalVisible = true;
    },
    toggleSearch() {
      this.isSearchVisible = !this.isSearchVisible;

      document.getElementById("block-search").addEventListener("click", e => {
        if (this.isSearchVisible) {
          this.closeSearch;
        }
      });
    },
    closeSearch(e) {
      if (
        this.isSearchVisible &&
        e.target != document.getElementById("block-search")
      ) {
        this.isSearchVisible = false;
      }
    },
    searchInit() {
      this.isSearchVisible = false;
    },
    changeModalVisible() {
      this.isModalVisible = false;
    }
  },
  created() {}
};
</script>
