<template>
  <section class="popular-tours" id="popular-tours" v-if="data && data.enable">
    <div class="container">
      <div class="popular-tours__inner">
        <div class="popular-tours__titles">
          <h2 class="title" v-html="data.title"></h2>

          <div class="months">
            <swiper
              class="swiper months__swiper"
              :options="swiperOptionThumbs"
              ref="swiperSmall"
            >
              <swiper-slide v-for="(month, i) in data.months" v-bind:key="i">
                <p class="months__name" v-html="month.name"></p>
              </swiper-slide>
              <div
                class="months__swiper-button months__swiper-button--tours-left"
                slot="button-prev"
              ></div>
              <div
                class="months__swiper-button months__swiper-button--tours-right"
                slot="button-next"
              ></div>
            </swiper>
          </div>
          <div class="popular-tours__more">
            <a href="#" v-html="data.more"></a>
          </div>
        </div>

        <div class="popular-tours__swiper-wrapper">
          <swiper
            class="swiper popular-tours__swiper"
            ref="swiperBig"
            :options="swiperOptionTop"
          >
            <swiper-slide
              class="popular-tours__slide"
              v-for="(slide, k) in data.slides"
              :key="k"
            >
              <div class="popular-tours__items">
                <div
                  class="popular-tours__item"
                  v-for="(tour, j) in slide.tours"
                  :key="j"
                >
                  <img :src="tour.img" :alt="tour.name" />
                  <div class="popular-tours__item-info">
                    <div class="popular-tours__item-dates">
                      <svg
                        class="icon"
                        width="12"
                        height="12"
                        aria-hidden="true"
                      >
                        <use xlink:href="#clock"></use>
                      </svg>
                      <span v-html="tour.dates"></span>
                    </div>

                    <p class="popular-tours__item-title" v-html="tour.name"></p>
                    <a
                      :href="tour.link"
                      class="button button--show-details"
                      v-html="data.button_more"
                    ></a>
                  </div>
                </div>
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "Services",
  computed: {},
  data() {
    return {
      swiperOptionTop: {
        loop: true,
        loopedSlides: 3, // looped slides should be the same
        spaceBetween: 10,
        slidesPerView: 1,
        centeredSlides: true
      },
      swiperOptionThumbs: {
        loop: true,
        loopedSlides: 3, // looped slides should be the same
        spaceBetween: 10,
        centeredSlides: false,
        slidesPerView: 1,
        touchRatio: 0.2,
        slideToClickedSlide: true,
        navigation: {
          nextEl: ".months__swiper-button--tours-right",
          prevEl: ".months__swiper-button--tours-left"
        }
      }
    };
  },
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  methods: {},
  watch: {},
  mounted() {
    this.$nextTick(() => {
      const swiperBig = this.$refs.swiperBig.$swiper;
      const swiperSmall = this.$refs.swiperSmall.$swiper;
      swiperBig.controller.control = swiperSmall;
      swiperSmall.controller.control = swiperBig;
    });
  },
  beforeDestroy() {}
};
</script>
