<template>
  <section class="contacts" v-if="data && data.enable">
    <div class="contacts__background" :style="style">
      <div class="container">
        <h1 v-html="data.title"></h1>
      </div>
    </div>
    <div class="contacts__body">
      <div class="container">
        <h2 class="contacts__schedule" v-html="data.schedule"></h2>
        <p class="contacts__description">
          Компания Алем-Тур – один из старейших туроператоров на рынке
          Республики Карелия. С 2002 года мы занимаемся разработкой и
          формированием экскурсионных, праздничных и иных пакетов для всех
          сезонов (в Едином Федеральном реестре туроператоров
          <a class="contacts__registry" :href="data.link">
            реестровый номер РТО 000877</a
          >, фингарантии - 500000 р.).
          <a class="contacts__about" :href="data.about">О нас >>></a>
        </p>
        <div class="contacts__items">
          <div class="contacts__item" v-for="(item, i) in data.items" :key="i">
            <p class="contacts__dept" v-if="item.dept" v-html="item.dept"></p>
            <p class="contacts__city" v-if="item.city" v-html="item.city"></p>
            <a
              class="contacts__phone"
              :href="'tel:' + item.number"
              v-html="item.number"
            ></a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "contacts-page",
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  computed: {
    style() {
      return this.data.image
        ? {
            backgroundImage: "url(" + this.data.image + ")"
          }
        : {};
    }
  }
};
</script>

