<template>
  <div class="breadcrumbs" v-if="breadcrumbs">
    <div class="breadcrumbs__inner">
      <div class="breadcrumbs__list">
        <div
          v-for="(breadcrumb, b) in breadcrumbs"
          v-bind:key="b"
          class="breadcrumbs__item"
        >
          <router-link :to="breadcrumb.href" v-html="breadcrumb.name">
          </router-link>
          <span class="breadcrumbs__divider">></span>
        </div>
      </div>
      <h1>
        <span v-html="tour.title"></span>
        <div class="tour__rating" v-if="tour.rating">
          <div class="tour__stars">
            <span class="tour__stars-item" v-for="n in 5" :key="n">
              <i :class="{ filled: n <= tour.rating }"></i>
            </span>
          </div>
        </div>
      </h1>
    </div>
  </div>
</template>

<script>
export default {
  name: "Breadcrumbs",
  data() {
    return {};
  },
  props: {
    tour: Object,
  },
  computed: {
    breadcrumbs() {
      return this.$store.state.breadcrumbs;
    },
    title() {
      return this.$store.state.page.id
        ? this.$store.state.page.title
        : "Страница не найдена";
    },
  },
};
</script>
