<template>
  <div>
    <div
      class="tours__items"
      v-masonry="data.tours_list"
      transition-duration="0.3s"
      item-selector=".tour"
      gutter="30"
    >
      <div
        v-masonry-tile
        class="tour"
        v-for="(tour, t) in data.tours_list"
        :key="t"
      >
        <router-link class="tour__image" :to="'/tours/' + tour.id">
          <span
            class="tour__image-sale"
            v-if="tour.sale"
            v-html="tour.sale"
          ></span>
          <img :src="tour.image" :alt="tour.title" />
          <div class="tour__image-tags">
            <span class="tour__image-tag" v-for="(tag, t) in tour.tags" :key="t"
              >#{{ tag }}</span
            >
          </div>
        </router-link>
        <div class="tour__texts">
          <router-link
            v-for="(cat, c) in tour.categories"
            :key="c"
            class="tour__category"
            :to="cat.link"
          >
            <span v-html="cat.name"></span>
          </router-link>
          <router-link
            class="tour__title"
            v-html="tour.title"
            :to="tour.link"
          ></router-link>
          <p class="tour__desc" v-html="tour.description"></p>
          <hr />
          <p class="tour__text" v-html="tour.month"></p>
          <p v-if="tour.next_check_in" class="tour__text">
            <span class="tour__text" v-html="tour.next_check_in.title"></span>
            <span
              class="tour__text tour__text--bold"
              v-html="tour.next_check_in.dates"
            ></span>
          </p>
          <p class="tour__text tour__text--bold" v-html="tour.days"></p>
          <p class="tour__price" v-html="tour.price"></p>
          <div class="tour__rating" v-if="tour.rating">
            <div class="tour__stars">
              <span class="tour__stars-item" v-for="n in 5" :key="n">
                <i :class="{ filled: n <= tour.rating }"></i>
              </span>
            </div>
            <router-link class="tour__reviews" :to="tour.reviews.link">
              <span v-html="tour.reviews.amount"></span>
              <span v-html="tour.reviews.text"></span>
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <Callback :callback="data.callback" class="callback--category" />
  </div>
</template>

<script>
import Callback from "./Callback.vue";
export default {
  name: "ToursList",
  computed: {},
  props: {
    data: Object,
  },
  methods: {},
  components: {
    Callback,
  },
};
</script>
