<template>
  <div
    :class="'banner ' + data.class"
    :style="style"
    v-if="data && data.enable"
  >
    <div class="container">
      <div class="banner__body">
        <div class="banner__content">
          <p class="banner__subtitle" v-html="data.subtitle"></p>
          <h1 v-html="data.title"></h1>
        </div>
        <Form :goal="data.goal" />
        <p class="banner__text" v-html="data.text"></p>
      </div>
    </div>
  </div>
</template>

<script>
import Form from "./Form.vue";

export default {
  name: "Banner",
  components: {
    Form
  },
  data() {
    return {
      swiperOptions: {
        navigation: {
          nextEl: ".swiper-advantages-next",
          prevEl: ".swiper-advantages-prev"
        },
        breakpoints: {
          0: {
            slidesPerView: 1,
            spaceBetween: 20
          }
        }
      }
    };
  },
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  computed: {
    isMobile() {
      return this.$root.isMobile;
    }
  }
};
</script>
